@import "./_activity-variables.less";
@import "./_activity-mixins.less";

.iconfont-face(@activity-icon-name, @activity-icon-path);

.@{activity-icon-name} {
	.iconfont-mixin(@activity-icon-name);
}

.@{container-class}{
	background: @container-bg-color;
	overflow: hidden;

	i,em{
		font-style: normal;
	}
	.mt2{
		margin-top: 2%;
	}
	.slim-content{
		width: 93.6%;
		margin-left: auto;
		 margin-right: auto
	}
	.vip{
		border:1px #e60012 solid;
		color:#e60012;
		font-size:@font-size-sm;
		.relative(-2px,_,_,0);
		border-radius: 2px;
		padding:0 3px;
		font-style: normal;
	}
	.unable-sales{
		position: relative;

		&:after{
			content:"";
			.absolute(0,_,_,0);
			.size(100%);
			background: data-uri('@{activity-image-path}/qiangguangguang.png') center no-repeat;
			background-size: 80% 80%;
		}
	}

	.labeling{
		font-style: normal;
		border:1px #bb098f solid;
		padding:0 2px;
		background: #fff;
		border-radius: 2px;
		font-size:@font-size-sm;
		margin-right:4px;

		&-zy{
			background: #ffefef;
			color:@price-color;
			border-color:@price-color;
		}
		&-store,&-direct{
			color:#bb098f;
		}
		&-global{
			color:#7c155e;
		}
	}
}



