@import "../_activity-variables.less";
@import "../_activity-mixins.less";

.fn-@{activity-ns}-lottery {
	background: #fff;

	.title{
		padding: 5*@rex;
		background: #f9f9f9;

		.ht{
			color: @ht-color;
		}
	}

	.info{
		font-size: 12*@rex;
		padding: 5*@rex;
		overflow: hidden;
	}

	.my-coupon{
		padding: 5*@rex;
		overflow: hidden;
		display: block;
	}

	.rule-dialog{
		display: none;

		&:before{
			.fixed(0,0,0,0);
			z-index: @activity-zindex-lottery;
			background: rgba(0, 0, 0, .3);
			content: '';
		}

		.dialog-content {
			.fixed(50%, _, _, 50%);
			z-index: @activity-zindex-lottery;
			transform: translate(-50%, -50%);
			background: #0a0a0a;
			color: #fff;
			padding: 10*@rex;
			width: 80%;
			border-radius: 3*@rex;

			.inner{
				position: relative;
			}

			.close {
				.absolute(-20*@rex,-10*@rex,_,_);
				font-size:36*@rex;
			}

			.dialog-title {
				line-height: 2;
				text-align: center;
				font-size: 16*@rex;
				border-bottom: 1*@rex solid #fff;
			}

			.text{
				padding: 10*@rex 0;
			}
		}
	}

	&.show-rule{
		.rule-dialog{
			display: block;
		}
	}

	.btn{
		color: #ff2626;
		background: #fffd77;
		border-radius: 3*@rex;
		padding: 5*@rex 0;
		box-shadow: 0*@rex 0*@rex 10*@rex #000;
		display: inline-block;
		vertical-align: top;
		text-align: center;
	}

	.state{
		position: relative;

		&:before{
			padding-top: 40%;
			content: '';
			display: block;
		}

		.bg{
			.absolute(0,_,_,0);
			.size(100%);
		}
	}

	.state-begin{
		&:before{
			padding-top: 40%;
		}

		.btns{
			.absolute(60%, _, _, 0);
			text-align: center;
			width: 100%;

			> a{
				width: 46%;
			}
		}
	}

	.state-success{
		&:before{
			padding-top: 40%;
		}

		.btns{
			.absolute(40%, _, _, 40%);

			> a{
				width: auto;
				padding-left: 10*@rex;
				padding-right: 10*@rex;
			}
		}
	}

	.state-fail{
		.btns{
			.absolute(66%, _, _, 0);
			width: 100%;
			text-align: center;

			> a{
				width: 46%;
			}
		}
	}

	.state-action{
		position: relative;

		.before,.after{
			.size(1.6%, 100%);
			content: '';
			.absolute(0,_,_,_);
			background-repeat: no-repeat;
			background-size: 100% 100%;
		}

		.before{
			left: 0;
			background-image: data-uri('@{activity-image-path}/lottery-gear-left.png');
		}

		.after{
			right: 0;
			background-image: data-uri('@{activity-image-path}/lottery-gear-right.png');
		}

		.inner{
			.size(96.8%, 100%);
			.absolute(0,_,_,1.6%);
			padding: 1.2% 0;
			background: #ff2626;
		}

		.canvas{
			.size(100%);
			background: #fff;
			position: relative;
			overflow: hidden;
			background-repeat: no-repeat;
			background-size: 100% 100%;

			canvas{
				.absolute(0,_,_,0);
			}
		}
	}
}
